import Cookies from 'js-cookie';

export default {
  name: 'Index',
  data() {
    return {
      name: '1',
    };
  },
  computed: {
    islogin() {
      return Cookies.get('user');
    },
    setStyle() {
      return {
        background: 'var(--main)!important',
      };
    },
    userInfo() {
      if (Cookies.get('user')) {
        return JSON.parse(Cookies.get('user')).username;
      }
      return false;
    },
  },
  mounted() {
    if (Cookies.get('FIRST_OPEN')) {
      this.$router.push({
        name: 'index',
      });
    } else {
      this.$router.push({
        name: 'navigation',
      });
    }
    if (Cookies.get('user')) {
      this.name = '2';
      this.$router.push({
        name: 'blog',
      });
    }
  },
  methods: {
    handleMenu(name) {
      if (name === '8-1') {
        this.logout();
      }
    },
    logout() {
      Cookies.remove('user');
      this.$router.push({
        name: 'login',
      });
    },
  },
};
