import { getHome } from '@/actions';
import { assign } from 'lodash';
// import { host } from '@/utils/config';

export default {
  data() {
    return {
      baseUrl: process.env.BASE_URL,
      videohost: '@/views/assets/video/index.mp4',
      data: {
        title: '',
        detail: '',
        slogan: '',
      },
    };
  },
  async mounted() {
    const { data } = await getHome();
    assign(this.data, data);
  },
  methods: {},
};
