import Cookies from 'js-cookie';

import crystalBall from './components/CrystalBall';

export default {
  name: 'Navigation',
  components: {
    crystalBall,
  },
  data() {
    return {
    };
  },

  mounted() {
    if (Cookies.get('FIRST_OPEN')) {
      this.$router.push({ name: 'index' });
    }
  },
  methods: {
    start() {
      Cookies.set('FIRST_OPEN', true);
      this.$router.push({ name: 'index' });
    },
  },
};
