import { postRegister } from '@/actions';

export default {
  data() {
    return {
      formInline: {
        user: '',
        password: '',
      },
      ruleInline: {
        user: [
          { required: true, message: '', trigger: 'blur' },
          { type: 'email', message: '', trigger: 'blur' },
        ],
        password: [
          {
            required: true,
            message: '',
            trigger: 'blur',
          },
          {
            type: 'string',
            min: 6,
            message: '6',
            trigger: 'blur',
          },
        ],
      },
    };
  },
  methods: {
    async handleSubmit(name) {
      this.$refs[name].validate(async (valid) => {
        if (valid) {
          const { code, msg } = await postRegister({
            username: this.formInline.user,
            password: this.formInline.password,
          });
          if (code === 200) {
            this.$Notice.info({
              title: '',
              desc: msg,
            });
            this.$router.push({ name: 'login' });
          } else {
            this.$Notice.error({
              title: '',
              desc: msg,
            });
          }
        }
      });
    },
    goLogin() {
      this.$router.push({ name: 'login' });
    },
  },
};
