import { postLogin } from '@/actions';
import Cookies from 'js-cookie';

export default {
  data() {
    return {
      formInline: {
        user: '',
        password: '',
      },
      ruleInline: {
        user: [{
          required: true,
          message: '',
          trigger: 'blur',
        }],
        password: [{
          required: true,
          message: '',
          trigger: 'blur',
        },
        {
          type: 'string',
          min: 6,
          message: '6',
          trigger: 'blur',
        },
        ],
      },
    };
  },
  methods: {
    handleSubmit(name) {
      this.$refs[name].validate(async (valid) => {
        if (valid) {
          const { code, msg, data } = await postLogin({
            username: this.formInline.user,
            password: this.formInline.password,
          });

          if (code === 200) {
            this.$Notice.info({
              title: '',
              desc: msg,
            });
            Cookies.set('user', data);
            this.$router.push({ name: 'index' });
          } else {
            this.$Notice.error({
              title: '',
              desc: msg,
            });
          }
        }
      });
    },
    goGister() {
      this.$router.push({ name: 'register' });
    },
  },
};
